.nav {
  $this: &;

  ul {
    display: flex;
    align-items: center;
    justify-content: space-around; }

  li {
    margin-left: rem(10);
    margin-right: rem(10); }

  a {
    @include tr(color .3s);
    @include hover-focus {
      @extend #{$this}__current-item; } }

  &__current-item {
    color: $accent; }

  &--footer {
    ul {
      display: block; }

    li {
      margin-left: 0;
      margin-right: 0;

      @include notlast {
        margin-bottom: rem(10); } } } }

